import s from './Settings.module.css'
import React from 'react'
import Preloader from '../Common/Preloader/Preloader'
import NormalModeSettings from './NormalModeSettings'
import EditModeSettingsForm from './EditModeSettings'

const Settings = ({profile, changeProfile, ...props}) => {
    if (!profile) {
        return <Preloader/>
    }
    let myContacts = [],
        selectContacts = []
    Object.keys(profile.contacts).map(key => {
        if (profile.contacts[key]) {
            myContacts.push(<li>{`${key} : ${profile.contacts[key]}`}</li>)
        }
        selectContacts.push(<option>{key}</option>)
    })

    return (
        <div>
            {!changeProfile ? <NormalModeSettings profile={profile} myContacts={myContacts}/> :
                <EditModeSettingsForm onSubmit={props.onSubmit} selectContacts={selectContacts}
                                  handleChange={props.handleChange} initialValues={profile}
                                  haveAJob={props.haveAJob} contactValue={props.contactValue}/>}
            {!changeProfile &&
            <button type={'button'} onClick={props.changeProfileOn}>Change profile</button>}
        </div>
    )
}
export default Settings