import s from './Settings.module.css'
import React from 'react'

const NormalModeSettings = ({profile, myContacts}) => {
    return (
        <div>
            <span>
                <label>Полное имя :</label>
                <div>{profile.fullName}
                </div>
            </span>
            <div>
                <label>Avatar </label>
                <img src={profile.photos.large}/>
            </div>
            <span>
                <label>{`About me: `}</label>
                <div>{profile.aboutMe}
                </div>
            </span>
            <span>
                <label>Contacts:</label>
                {<div>{!myContacts[0] ? 'Нет контактов' : <ul>{myContacts}</ul>}</div>}
            </span>
            <span>{!profile.lookingForAJob ? 'Поиск работы : не ищу' : `Поиск работы : ${profile.lookingForAJobDescription}`}
            </span>
        </div>
    )
}
export default NormalModeSettings