import s from './Settings.module.css'
import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { FormControl } from '../Common/FormsControls/FormsControls'

const EditModeSettings = ({profile, handleSubmit, error, ...props}) => {

    return (
        <form onSubmit={handleSubmit}>
            <div>
                <label>Полное имя :</label>
                <Field component={FormControl}
                       typeField={'input'}
                       name={'fullName'}
                       placeholder={'Введите имя'}/>
            </div>
            <label>Avatar </label>
            <input type={'file'}
                   accept="image/*"
                   id={'avatar'}
                   onChange={(event) => props.handleChange(event)}/>
            <div>
                <label>About me: </label>
                <Field component={FormControl}
                       typeField={'input'}
                       name={'aboutMe'}
                       placeholder={'Напишите о себе'}/>
            </div>
            <div>
                <label>Select contact</label>
                <Field name={'selectContact'}
                       component={FormControl}
                       typeField={'select'}>
                    <option></option>
                    {props.selectContacts}
                </Field>
                {
                    props.contactValue &&
                    <Field component={FormControl}
                           typeField={'input'}
                           autoFocus
                           name={`contacts.${props.contactValue}`}/>
                }
            </div>
            <div>
                <label>I'm looking a job</label>
                <Field component={FormControl} typeField={'input'} type={'checkbox'}
                       name={'lookingForAJob'}/>
                {
                    props.haveAJob &&
                    <div>
                        <label>What kind of job?</label>
                        <Field component={FormControl} typeField={'input'}
                               name={'lookingForAJobDescription'}/>
                    </div>
                }
            </div>
            {error &&
            <div>{error}</div>}
            <div>
                <button type={'submit'}>Save changes</button>
            </div>
        </form>
)
}

const EditModeSettingsForm = reduxForm({form: 'settings', enableReinitialize: true})(EditModeSettings)

export default EditModeSettingsForm